import { PureComponent } from 'react';
//import React from 'react';
import useForm from 'react-hook-form';
import { Button, Modal } from 'semantic-ui-react';

const FormCustomizer = (data: any) => {
	const { register, handleSubmit, errors } = useForm();

	const closeModal = () => {};

	const onSubmit = (data: any) => {
		console.log(data);
		closeModal();
	};

	console.log(errors);

	return (
		<form onSubmit={handleSubmit(onSubmit)}>
			<label>
				Símbolo decimal:
				<select name='simbolo-decimal' ref={register({ required: true })}>
					<option value=','>,</option>
					<option value='.'>.</option>
					<option value='&#39;'>'</option>
				</select>
			</label>
			<label>
				Números, de dígitos después del decimal:
				<select name='numeros-digitos-despues-del-decimal' ref={register({ required: true })}>
					<option value='0'>0</option>
					<option value='1'>1</option>
					<option value='2'>2</option>
					<option value='3'>3</option>
					<option value='4'>4</option>
					<option value='5'>5</option>
					<option value='6'>6</option>
					<option value='7'>7</option>
					<option value='8'>8</option>
					<option value='9'>9</option>
				</select>
			</label>
			<label>
				Símbolo de agrupación de dígitos:
				<select name='simbolo-de-agrupacion-de-digitos' ref={register({ required: true })}>
					<option value=','>,</option>
					<option value='.'>.</option>
					<option value='&#39;'>'</option>
				</select>
			</label>

			<label>
				Agrupación de dígitos:
				<select name='Agrupacion-de-digitos' ref={register({ required: true })}>
					<option value='123456789'>123456789</option>
					<option value='123,456,789'>123,456,789</option>
					<option value='123456,789'>123456,789</option>
					<option value='12,34,56,789'>12,34,56,789</option>
				</select>
			</label>

			<select name='Símbolo de signo negativo:' ref={register({ required: true })}>
				<option value='-'>-</option>
			</select>
			<select name='Formato de número negativo:' ref={register({ required: true })}>
				<option value='(1.1)'>(1.1)</option>
				<option value='-1.1'>-1.1</option>
				<option value='1.1-'>1.1-</option>
			</select>
			<select name='Mostrar ceros a la izquierda:' ref={register({ required: true })}>
				<option value='.7'>.7</option>
				<option value='0.7'>0.7</option>
			</select>
			<select name='Separador de lista' ref={register({ required: true })}>
				<option value='&#39;,&#39;'>','</option>
				<option value='&#39;'>'</option>
				<option value='&#39;'>'</option>
			</select>
			<select name='Sistema de medida:' ref={register({ required: true })}>
				<option value='U.S.'>U.S.</option>
				<option value=' Metric'> Metric</option>
			</select>
			<input
				type='text'
				placeholder='Dígitos estándar:'
				name='Dígitos estándar:'
				ref={register({ required: true, max: 9, min: 9, maxLength: 9, pattern: /[0-9]{9}/i })}
			/>
			<select name='Use dígitos nativos:' ref={register({ required: true })}>
				<option value='Context'>Context</option>
				<option value=' Never'> Never</option>
				<option value=' National'> National</option>
			</select>

			<div className='actions ab-status-actions'>
				<Button onClick={closeModal} className='ui compact primary center button' enabled>
					Cancelar
				</Button>
				<Button className='ui compact primary center button' type='submit'>
					Guardar
				</Button>
			</div>
		</form>
	);
};

type Props = {
	data: Formats;
};

type Formats = {
	name: string;
};

type CustomizeState = {
	openModal: boolean;
};

export class Customize extends PureComponent<Props, CustomizeState> {
	constructor(props: Props, customizeState: CustomizeState) {
		super(props);
		this.state = {
			openModal: true
		};
	}

	closeModalConfirm = () => {
		this.setState({ openModal: false });
	};

	render() {
		console.log(this.props);
		return (
			<Modal size='tiny' open={this.state.openModal} closeOnEscape={true} closeOnDimmerClick={false} closeIcon>
				<Modal.Header> PERSONALIZAR FORMATO </Modal.Header>
				<Modal.Content>
					<FormCustomizer data={this.props} />
				</Modal.Content>
			</Modal>
			//{!this.state.openModal && <EpaycoNormal data={this.props.data.table} />}
		);
	}
}
